.btn--link, .btn--burger {
  display: flex;
  flex-direction: column;
  width: 30px;
  height: 20px;
}
.btn--link span, .btn--burger span {
  border-radius: 10px;
  width: 100%;
  height: 3px;
  background: #000;
}
.btn--link span:not(:last-child), .btn--burger span:not(:last-child) {
  margin-bottom: 5px;
}

.btn {
  display: block;
  cursor: pointer;
}
.btn.btn--photo {
  padding: 14px 25px 14px 19px;
  width: 214px;
  font-size: 14px;
}
.btn.btn--photo::before {
  content: "";
  display: block;
  margin-right: 8px;
  width: 15px;
  height: 15px;
  background: url("../img/icons/icon-upload-photo.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.btn.btn--photo::after {
  margin-left: auto;
}
.btn--border {
  border: 2px solid var(--color-1);
  border-radius: 30px;
  padding: 14px;
  width: 100%;
  max-width: min(214px, 42vw);
  font-weight: 600;
  font-size: max(1rem, 12px);
  letter-spacing: 0.33em;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-white);
}
.btn--solid {
  display: flex;
  justify-content: space-between;
  border: unset;
  border-radius: 5px;
  padding: 14px 20px;
  width: 100%;
  max-width: 225px;
  font-family: "WorkSans", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: var(--color-white);
  background: var(--color-4);
}
.btn--solid-second {
  background: var(--color-first);
}
.btn--arrow {
  display: flex;
  align-items: center;
}
.btn--arrow::after {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  min-width: 12px;
  background: url("../img/icons/icon-btn-arrow.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.btn--link {
  color: var(--color-first);
}
.btn--contact {
  display: flex;
  align-items: center;
  justify-content: start;
  width: 100%;
  max-width: 267px;
}
.btn--contact::before {
  content: "";
  display: block;
  margin-right: 15px;
  width: 24px;
  height: 24px;
  min-width: 24px;
  background: url("../img/icons/icon-btn-contact.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.btn--contact::after {
  margin-left: auto;
}

form {
  font-family: "WorkSans";
}
form textarea,
form input:not([type=file], [type=checkbox]) {
  border: unset;
  border-radius: 5px;
  padding: 16px 21px;
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: var(--color-first);
  background: var(--color-2);
}
form textarea::placeholder,
form input:not([type=file], [type=checkbox])::placeholder {
  font-weight: 300;
  color: var(--color-3);
}
form textarea {
  width: calc(100% - 30px);
  resize: none;
}
form .show-password {
  position: absolute;
  right: 25px;
  bottom: 14px;
  display: block;
  margin: unset;
  padding: unset;
  width: 18px;
  height: 18px;
  background: url("../img/icons/icon-hide-password.svg") no-repeat;
  background-size: contain;
  cursor: pointer;
}
form .show-password:checked {
  opacity: 0.3;
}
form label {
  margin-bottom: 6px;
  padding-left: 15px;
  font-weight: 600;
  font-size: 13px;
  color: var(--color-first);
}
form .btn--link {
  margin-top: 35px;
  padding-left: 15px;
  min-width: max-content;
  font-weight: 600;
  font-size: 13px;
}
form .form_input_wrap {
  display: flex;
  flex-direction: column-reverse;
}
form .form_input_wrap.password {
  position: relative;
}
form.type-1 {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  max-width: 417px;
  gap: 25px;
}
form.type-1 .btn {
  margin-top: 75px;
}

.nav {
  display: flex;
  align-items: stretch;
  background: var(--color-2);
}
.nav__logo {
  padding: 23px 26px;
  font-size: 0;
  background: var(--color-5);
}
.nav__logo img {
  width: 29px;
  height: 36px;
}
.nav__title {
  position: relative;
  display: flex;
  align-items: center;
  padding: 10px 14px 10px 30px;
  min-height: 100%;
  max-width: min-content;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: var(--color-3);
}
.nav__title::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 1px;
  height: 100%;
  background: #e5eaf5;
}
.nav__back {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 35px;
  font-family: "WorkSans", sans-serif;
}
.nav__back a {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 14px;
}
.nav__back a::before {
  content: "";
  display: block;
  margin-right: 5px;
  margin-left: 2px;
  width: 12px;
  height: 12px;
  background: url("../img/icons/icon-arrow-back.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.nav__back span {
  font-weight: 300;
  font-size: 24px;
  line-height: 28px;
  color: var(--color-6);
}
.nav__back.project {
  flex-direction: row;
  align-items: center;
}
.nav__right {
  display: flex;
  align-items: center;
  margin-right: 65px;
  margin-left: auto;
}
.nav__right .btn {
  width: 214px;
  font-size: 14px;
}
.nav__search {
  margin-right: 37px;
  width: 20px;
  height: 20px;
  background: url("../img/icons/icon-search.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.nav__notifications {
  margin-right: 25px;
  width: 20px;
  height: 20px;
  background: url("../img/icons/icon-notifications.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.nav__profile-img {
  width: 32px;
  height: 32px;
}
.nav__settings {
  display: block;
  margin-left: 30px;
  width: 20px;
  height: 20px;
  background: url("../img/icons/icon-setting.svg") no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
}
.nav__down {
  display: block;
  margin-right: 12px;
  margin-left: auto;
  width: 20px;
  height: 20px;
  background: url("../img/icons/icon-arrow-back.svg") no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.8;
  transform: rotate(-90deg);
  cursor: pointer;
}

span .select2-selection__arrow {
  top: 50% !important;
  height: 10px !important;
  transform: translateY(-50%);
}
span .select2-selection__arrow b {
  left: unset !important;
  top: unset !important;
  margin-top: unset !important;
  margin-left: unset !important;
  border: unset !important;
  width: 10px !important;
  height: 10px !important;
  background: url("../img/icons/icon-arrow-dropdown.svg") no-repeat;
  background-position: center;
  background-size: contain;
  transition: var(--anim2);
}
span.select2-container .select2-selection--single {
  padding: 16px 21px;
  height: unset;
}
span.select2-container--open .select2-selection__arrow b {
  transform: rotate(180deg);
}
span.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: var(--color-second) !important;
}
span.select2-container--default .select2-selection--single {
  border: unset;
  border-radius: 5px;
  font-weight: 300;
  background-color: var(--color-2);
}
span.select2-container--default .select2-selection--single .select2-selection__rendered {
  padding: unset;
  line-height: 1;
  color: var(--color-6);
}
span.select2-container--below .select2-selection__rendered {
  font-weight: 500 !important;
}
span.select2-dropdown {
  border: unset;
  color: var(--color-6);
  background-color: var(--color-2);
}
span.select2-dropdown .select2-results__option {
  padding: 11px 20px;
}
span.select2-dropdown .select2-results__option:nth-child(1) {
  display: none;
}

.form-profile {
  margin: 0 auto;
  padding: 30px 0;
  max-width: 1350px;
}
.form-profile .row {
  display: flex;
}
.form-profile .row-field:not(:last-child) {
  margin-bottom: 24px;
}
.form-profile .row.base {
  margin-bottom: 65px;
}
.form-profile .row.contact {
  margin-bottom: 55px;
}
.form-profile .row.picture {
  margin-bottom: 60px;
}
.form-profile .row.about, .form-profile .row.company {
  margin-bottom: 30px;
}
.form-profile .row.membership {
  margin-top: 77px;
}
.form-profile .row.notifications {
  margin-top: 90px;
}
.form-profile .row.notifications .form_input_wrap:not(:last-child) {
  margin-bottom: 16px;
}
.form-profile .col.fields {
  width: 100%;
}
.form-profile .col.fields .form_input_wrap {
  width: calc(50% - 30px);
}
.form-profile .col.fields .form_input_wrap:not(:last-child) {
  margin-right: 30px;
}
.form-profile .col.fields.textarea .form_input_wrap {
  width: 100%;
}
.form-profile .col.title {
  padding-left: 7.4%;
  width: 49%;
  font-weight: 300;
  font-size: 24px;
  line-height: 1;
  color: var(--color-6);
}
.form-profile .col.title span {
  font-weight: 500;
  color: var(--color-warning);
}
.form-profile .picture .form_input_wrap {
  flex-direction: row;
  align-items: center;
  width: max-content;
}
.form-profile .picture .form_input_wrap input {
  display: none;
}
.form-profile .add-photo {
  overflow: hidden;
  margin-right: 25%;
  border-radius: 100%;
  padding: 25px;
  width: 84px;
  height: 84px;
  min-width: 84px;
  background: var(--color-4);
}
.form-profile .add-photo span {
  display: block;
  width: 100%;
  height: 100%;
  background: url("../img/icons/icon-add-photo.svg") no-repeat;
  background-size: contain;
}
.form-profile .add-photo.show {
  padding: unset;
}
.form-profile .add-photo.show span {
  display: none;
}
.form-profile .add-photo.show #show-photo {
  display: flex;
  justify-content: center;
  width: 84px;
}
.form-profile .add-photo.show img {
  height: 84px;
  object-fit: cover;
}
.form-profile .btn--solid-second {
  padding: 16px 21px;
  min-width: max-content;
  font-size: 14px;
}
.form-profile .send {
  margin-top: 77px;
  margin-right: 30px;
  margin-left: auto;
  min-width: max-content;
}

input[type=checkbox]:checked + label {
  position: relative;
}
input[type=checkbox]:checked + label::before {
  background: var(--color-first);
}
input[type=checkbox]:checked + label::after {
  content: "";
  position: absolute;
  left: 3px;
  display: block;
  width: 10px;
  height: 10px;
  background: url("../img/icons/icon-checked.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
input[type=checkbox] + label {
  display: flex;
  align-items: center;
  padding-left: unset;
  cursor: pointer;
}
input[type=checkbox] + label::before {
  content: "";
  display: block;
  margin-right: 15px;
  border-radius: 4px;
  width: 16px;
  height: 16px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05), inset 0 5px 20px rgba(0, 0, 0, 0.03);
  background: #f8faff;
}

.dashboard-wrapper {
  display: flex;
  min-height: calc(100vh - 82px);
  overflow: hidden;
}
.dashboard-wrapper .profile__wrapper {
  width: 100%;
}
.dashboard-wrapper .sidebar {
  display: flex;
  flex-direction: column;
  padding: 25px 28px;
  background: var(--color-first);
  gap: 25px;
}
.dashboard-wrapper .sidebar img {
  width: 25px;
  height: 22px;
}

.projects .projects-wrapper {
  flex-direction: column;
}

.projects-wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
  width: 100%;
  height: 100%;
  max-width: 1300px;
  gap: 24px;
}

.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  background: rgba(48, 62, 101, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: var(--anim2);
}
.popup__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  transition: var(--anim4);
}
.popup__content {
  overflow: hidden;
  border-radius: 30px;
  width: 90%;
  max-width: 1190px;
  background: var(--color-white);
}
.popup__content-wrap {
  padding: 36px 50px 50px;
}
.popup__content .btn.send {
  margin-left: auto;
}
.popup__nav {
  display: flex;
  align-items: stretch;
  padding: 17px 48px 23px;
  background: var(--color-2);
}
.popup__nav-back a {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 14px;
}
.popup__nav-back a::before {
  content: "";
  display: block;
  margin-right: 5px;
  margin-left: 2px;
  width: 12px;
  height: 12px;
  background: url("../img/icons/icon-arrow-back.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.popup__nav-back span {
  font-weight: 300;
  font-size: 24px;
  line-height: 28px;
  color: var(--color-6);
}
.popup.open {
  z-index: 10;
  opacity: 1;
  visibility: visible;
}

.field-add {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #e5eaf5;
  border-radius: 5px;
  height: 160px;
  background: var(--color-7);
}
.field-add-btn {
  display: flex;
  margin: 0 auto 7px;
  border: 1px solid #e5eaf5;
  border-radius: 10px;
  width: 32px;
  height: 32px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
  background: #fff;
  cursor: pointer;
}
.field-add-btn::after {
  content: "";
  margin: auto;
  width: 7px;
  height: 7px;
  background: url("../img/icons/icon-plus.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.field-add-text {
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: var(--color-3);
}

.project__item {
  width: calc(50% - 12px);
  height: 166px;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.05), inset 0 5px 20px rgba(0, 0, 0, 0.03);
  font-family: "WorkSans", sans-serif;
}
.project__item-wrap {
  display: grid;
  grid-template-columns: 255px auto;
}
.project__item-favorite {
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 22px;
}
.project__item-favorite-wrap {
  display: flex;
  align-items: center;
  padding-right: 30px;
  width: 100%;
  background: var(--color-1);
}
.project__item-favorite .project__img {
  width: 145px;
  height: 94px;
}
.project__item-favorite .project__content {
  flex: 1;
  padding-left: 30px;
}
.project__item-favorite .project__type {
  display: flex;
  margin-right: 64px;
  gap: 20px;
}
.project__item-favorite .project__type span {
  border: 2px solid var(--color-4);
  border-radius: 10px;
  padding: 7px;
  width: 82px;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: var(--color-6);
}
.project-wrap {
  text-align: center;
}
.project__add {
  border: 1px solid #e5eaf5;
  border-radius: 5px;
  background: var(--color-7);
}
.project__add .project__item-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.project__add-btn {
  display: flex;
  margin: 0 auto 7px;
  border: 1px solid #e5eaf5;
  border-radius: 10px;
