.progress {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 48px;
  border-radius: 12px;
  width: 100%;
  height: 24px;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.05), inset 0 5px 20px rgba(0, 0, 0, 0.03);
  background: #e5eaf5;
}
.progress::after {
  content: "";
  position: absolute;
  left: 6px;
  right: 6px;
  top: 4px;
  bottom: 4px;
  overflow: hidden;
  border-radius: 8px 0 0 8px;
  width: 16.1%;
  height: 16px;
  background: var(--color-3);
}
.progress__item {
  position: relative;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.progress__item-top {
  position: absolute;
  top: -30px;
  width: max-content;
  max-width: 180px;
  font-size: 10px;
  line-height: 12px;
  color: var(--color-3);
}
.progress__item-point {
  border-radius: 100%;
  width: 8px;
  height: 8px;
  background: #fff;
}
.progress__item-bottom {
  position: absolute;
  left: 16.1%;
  top: 40px;
  display: flex;
  column-gap: 10px;
  align-items: center;
  font-size: 10px;
  line-height: 12px;
  transform: translateX(-44%);
}
.progress__item-bottom-next, .progress__item-bottom-prev {
  width: 24px;
  height: 24px;
  background: url("../img/icons/icon-progress-arrow.svg") no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
}
.progress__item-bottom-next {
  transform: rotate(180deg);
}
.progress__item-bottom-quantity {
  display: flex;
  column-gap: 3px;
}
.progress__item.done::after {
  content: "";
  position: absolute;
  bottom: -40px;
  width: 18px;
  height: 18px;
  background: url("../img/icons/icon-progress-done.svg") no-repeat;
  background-position: center;
  background-size: contain;
}
.progress__item.done .progress__item-point {
  background: #92e757;
}
.progress__item.current .progress__item-point {
  position: relative;
  background: #56ccf2;
}
.progress__item.current .progress__item-point::before {
  content: "";
  position: absolute;
  left: 3px;
  top: -15px;
  bottom: -15px;
  z-index: -1;
  display: block;
  width: 1px;
  background: #7583aa;
}
.progress.done::after {
  border-radius: 8px;
  width: calc(100% - 12px);
}

