  background-color: #eee;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  height: 100%;
}

.home-page {
  position: relative;
  padding: 55px 0 18px;
  color: var(--color-white);
}
.home-page::before {
  content: "";
  position: fixed;
  left: 0;
  right: 50%;
  top: 0;
  bottom: 0;
  background: var(--color-first);
}
@media screen and (max-width: 767px) {
  .home-page::before {
    right: 0;
    bottom: 50%;
  }
}
.home-page::after {
  content: "";
  position: fixed;
  left: 50%;
  right: 0;
  top: 0;
  bottom: 0;
  background: var(--color-second);
}
@media screen and (max-width: 767px) {
  .home-page::after {
    left: 0;
    top: 50%;
    bottom: 0;
  }
}
.home-page .container {
  position: relative;
  z-index: 5;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100vh - 83px);
}
.home-page__top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home-page__top-logo {
  border: 1px solid #7583aa;
  padding: 5px;
  font-size: 0;
}
.home-page__middle {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  gap: 40px;
}
.home-page__middle .col {
  width: 50%;
}
.home-page__middle .col.left .home-page__title-top::after {
  left: 0;
}
@media screen and (max-width: 767px) {
  .home-page__middle .col.left {
    background: var(--color-first);
  }
}
.home-page__middle .col.right {
  padding-left: 40px;
  text-align: right;
}
.home-page__middle .col.right .btn {
  margin-left: auto;
}
