.project-create {
	&-wrap {
		position: relative;
		width: 100%;
		font-family: "WorkSans", sans-serif;
		color: var(--color-6);

		.project-preview {
			&__author {
				&-settings {
					display: block;
					margin-left: auto;
					width: 20px;
					height: 20px;
					background: url("../img/icons/icon-setting.svg") no-repeat;
					background-position: center;
					background-size: contain;
					cursor: pointer;
				}

				&-favorite {
					display: block;
					margin-left: auto;
					width: 20px;
					height: 20px;
					background: url("../img/icons/icon-favorite.svg") no-repeat;
					background-position: center;
					background-size: contain;
					cursor: pointer;
				}
			}
		}

		&-img {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			z-index: -1;
			overflow: hidden;
			width: 100%;
			height: 260px;

			img {
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__content-wrap {
		margin: 200px auto 0;
		max-width: 1230px;
	}

	&__top {
		display: flex;
		background: var(--color-1);

		&-img {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 254px;
			background: var(--color-4);
			cursor: pointer;

			img {
				width: 75px;
				height: 75px;
			}
		}

		&-descr {
			flex: 1;
			padding: 28px 26px 20px 31px;

			&-top {
				display: flex;
				justify-content: space-between;
				margin-bottom: 14px;

				&-title {
					display: flex;
					flex-direction: column;
					font-weight: 300;
					font-size: 24px;
					line-height: 28px;

					span {
						font-weight: 600;
						font-size: 14px;
						line-height: 16px;
						color: var(--color-3);
					}
				}

				&-type {
					display: flex;
					height: min-content;

					span {
						display: block;
						border: 2px solid var(--color-4);
						border-radius: 10px;
						padding: 7px;
						min-width: 82px;
						font-size: 12px;
						line-height: 14px;
						text-align: center;

						&:not(:last-child) {
							margin-right: 17px;
						}
					}
				}
			}

			&-bottom {
				display: flex;
				flex-direction: column;
				justify-content: center;
				height: 160px;

				&--add {
					border: 1px solid #e5eaf5;
					border-radius: 5px;
					background: var(--color-7);

					.project__add-text {
						text-align: center;
					}
				}
			}

			.field-text__edit {
				margin-top: 20px;
			}
		}

		&-right {
			position: relative;
			width: 324px;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				width: 1px;
				box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
				background: #e5eaf5;
			}

			&-top {
				padding: 30px 15px;
				padding-right: 43px;
			}

			&-bottom {
				position: relative;
				padding: 12px 15px 22px;
				padding-right: 43px;
				font-size: 12px;
				line-height: 24px;
				color: var(--color-3);

				&::after {
					content: "";
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					width: 100%;
					height: 1px;
					box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
					background: #e5eaf5;
				}

				span {
					display: block;
					margin-top: 23px;
					font-weight: 300;
					font-size: 24px;
					line-height: 28px;
					color: var(--color-6);
				}

				&.full,
				.full {
					position: relative;
					display: flex;
					padding: unset;

					.right,
					.left {
						padding: 12px 15px;
						width: 50%;
					}

					.left {
						position: relative;

						&::after {
							content: "";
							position: absolute;
							right: 0;
							top: 0;
							bottom: 0;
							width: 1px;
							box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
							background: #e5eaf5;
						}
					}

					.right {
						.project__nda-user-wrap {
							margin-top: 20px;
						}
					}

					&-quantity {
						display: flex;
					}

					span {
						width: max-content;
						font-weight: 500;
						font-size: 24px;
						line-height: 28px;

						&:nth-child(1) {
							margin-right: 5px;
						}

						&:nth-child(2) {
							color: var(--color-4);
						}
					}
				}
			}

			.btn {
				max-width: 100%;
			}
		}
	}

	.field-text {
		p {
			font-weight: 400;
			font-size: 14px;
			line-height: 16px;
		}

		&__edit {
			display: flex;
			align-items: center;
			border: 1px solid #e5eaf5;
			border-radius: 10px;
			padding: 8px 20px 8px 8px;
			width: 133px;
			box-shadow: 0 8px 24px rgb(0 0 0 / 5%);
			font-weight: 400;
			font-size: 12px;
			line-height: 14px;
			color: var(--color-3);
			background: var(--color-white);
			cursor: pointer;

			&::before {
				content: "";
				display: block;
				margin-right: 35px;
				width: 12px;
				height: 12px;
				background: url("../img/icons/icon-edit.svg") no-repeat;
				background-position: center;
				background-size: contain;
			}
		}
	}

	.full-text {
		padding-bottom: 30px;

		p,
		img {
			padding-bottom: 10px;
			font-weight: 400;
			font-size: 14px;
			line-height: 16px;
		}
	}

	.fields {
		.title {
			margin-bottom: 30px;
		}
	}

	.project-idea {
		.project-preview__author {
			margin-top: 30px;
		}
	}
	p {
		font-size: 14px;
		line-height: 17px;
	}
}

.progress {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-around;
	margin-top: 48px;
	border-radius: 12px;
	width: 100%;
	height: 24px;
	box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.05), inset 0 5px 20px rgba(0, 0, 0, 0.03);
	background: #e5eaf5;

	&::after {
		content: "";
		position: absolute;
		left: 6px;
		right: 6px;
		top: 4px;
		bottom: 4px;
		overflow: hidden;
		border-radius: 8px 0 0 8px;
		width: 16.1%;
		height: 16px;
		background: var(--color-3);
	}

	&__item {
		position: relative;
		z-index: 5;
		display: flex;
		flex-direction: column;
		align-items: center;

		&-top {
			position: absolute;
			top: -30px;
			width: max-content;
			max-width: 180px;
			font-size: 10px;
			line-height: 12px;
			color: var(--color-3);
		}

		&-point {
			border-radius: 100%;
			width: 8px;
			height: 8px;
			background: #fff;
		}

		&-bottom {
			position: absolute;
			left: 16.1%;
			top: 40px;
			display: flex;
			column-gap: 10px;
			align-items: center;
			font-size: 10px;
			line-height: 12px;
			transform: translateX(-44%);

			&-next,
			&-prev {
				width: 24px;
				height: 24px;
				background: url("../img/icons/icon-progress-arrow.svg") no-repeat;
				background-position: center;
				background-size: contain;
				cursor: pointer;
			}

			&-next {
				transform: rotate(180deg);
			}

			&-quantity {
				display: flex;
				column-gap: 3px;
			}
		}

		&.done {
			&::after {
				content: "";
				position: absolute;
				bottom: -40px;
				width: 18px;
				height: 18px;
				background: url("../img/icons/icon-progress-done.svg") no-repeat;
				background-position: center;
				background-size: contain;
			}

			.progress__item-point {
				background: #92e757;
			}
		}

		&.current {
			.progress__item-point {
				position: relative;
				background: #56ccf2;

				&::before {
					content: "";
					position: absolute;
					left: 3px;
					top: -15px;
					bottom: -15px;
					z-index: -1;
					display: block;
					width: 1px;
					background: #7583aa;
				}
			}
		}
	}

	&.done {
		&::after {
			border-radius: 8px;
			width: calc(100% - 12px);
		}
	}
}
